import React, { Component } from 'react'
import { connect } from 'react-redux'
import PropTypes from 'prop-types'
import TextField from '../common/TextField'
import InputGroup from '../common/InputGroup'
import SelectList from '../common/SelectList'
import TextArea from '../common/TextArea'
import { createProfile, getCurrentProfile } from '../../actions/profileActions'
import { withRouter } from 'react-router'
import isEmpty from '../../validation/is_empty'

class CreateProfile extends Component {

    constructor(props) {
        super(props)
        this.state = {
            displaySocialInputs: false,
            handle: "",
            company: "",
            website: "",
            location: "",
            status: "",
            skills: "",
            githubusername: "",
            bio: "",
            twitter: "",
            facebook: "",
            linkedin: "",
            youtube: "",
            instagram: "",
            errors: {}
        }

        this.onChange = this.onChange.bind(this)
        this.onSubmit = this.onSubmit.bind(this)
    }

    componentDidMount(){
        this.props.getCurrentProfile()
    }

    onChange(e){
        this.setState({ [e.target.name]: e.target.value })
    }

    onSubmit(e){
        e.preventDefault()
        console.log('Submit')

        const profileData = {
            handle: this.state.handle,
            company: this.state.company,
            website: this.state.website,
            location: this.state.location,
            status: this.state.status,
            skills: this.state.skills,
            githubusername: this.state.githubusername,
            bio: this.state.bio,
            twitter: this.state.twitter,
            facebook: this.state.facebook,
            linkedin: this.state.linkedin,
            youtube: this.state.youtube,
            instagram: this.state.instagram
        }

        this.props.createProfile(profileData,this.props.history)
    }

    componentWillReceiveProps(nextProps) {
        if(nextProps.errors) {
            this.setState({errors: nextProps.errors})
        }

        if(nextProps.profile) {
            const profile = nextProps.profile.profile;

            // bring skills array back to , sep value
            const skillCSV = profile.skills.join(',')

            // if profile field doesn't exist make empty string
            profile.company = !isEmpty(profile.company) ? profile.company : ""
            profile.website = !isEmpty(profile.website) ? profile.website : ""
            profile.location = !isEmpty(profile.location) ? profile.location : ""
            profile.githubusername = !isEmpty(profile.githubusername) ? profile.githubusername : ""
            profile.bio = !isEmpty(profile.bio) ? profile.bio : ""
            profile.social = !isEmpty(profile.social) ? profile.social : {}
            profile.twitter = !isEmpty(profile.social.twitter) ? profile.social.twitter : ""
            profile.facebook = !isEmpty(profile.social.facebook) ? profile.social.facebook : ""
            profile.linkedin = !isEmpty(profile.social.linkedin) ? profile.social.linkedin : ""
            profile.youtube = !isEmpty(profile.social.youtube) ? profile.social.youtube : ""
            profile.instagram = !isEmpty(profile.social.instagram) ? profile.social.instagram : ""

            // set component field state
            this.setState({
                handle: profile.handle,
                company: profile.company,
                website: profile.website,
                location: profile.location,
                status: profile.status,
                skills: skillCSV,
                githubusername: profile.githubusername,
                bio: profile.bio,
                twitter: profile.twitter,
                facebook: profile.facebook,
                linkedin: profile.linkedin,
                youtube: profile.youtube,
                instagram: profile.instagram
            })
        }
    }

    render() {
        
        const { errors, displaySocialInputs } = this.state

        let socialInputs;

        if(displaySocialInputs){
            socialInputs = (
                <div>
                    <InputGroup 
                        placeholder="Twitter"
                        name="twitter"
                        icon="fab fa-twitter"
                        value={this.state.twitter}
                        onChange={this.onChange}
                        error={errors.twitter}
                    />

                    <InputGroup 
                        placeholder="Facebook"
                        name="facebook"
                        icon="fab fa-facebook"
                        value={this.state.facebook}
                        onChange={this.onChange}
                        error={errors.facebook}
                    />

                    <InputGroup 
                        placeholder="LinkedIn"
                        name="linkedin"
                        icon="fab fa-linkedin"
                        value={this.state.linkedin}
                        onChange={this.onChange}
                        error={errors.linkedin}
                    />

                    <InputGroup 
                        placeholder="Youtube"
                        name="youtube"
                        icon="fab fa-youtube"
                        value={this.state.youtube}
                        onChange={this.onChange}
                        error={errors.youtube}
                    />

                    <InputGroup 
                        placeholder="Instagram"
                        name="instagram"
                        icon="fab fa-instagram"
                        value={this.state.instagram}
                        onChange={this.onChange}
                        error={errors.instagram}
                    />

                </div>
            )
        }

        const options = [
            {
                label: '* Select Professional Status',
                value: 0
            },
            {
                label: 'Developer',
                value: 'Developer'
            },
            {
                label: 'Senior Developer',
                value: 'Senior Developer'
            },
            {
                label: 'Data Scientist',
                value: 'Data Scientist'
            },
            {
                label: 'Manager',
                value: 'Manager'
            },
            {
                label: 'Student',
                value: 'Student'
            },
            {
                label: 'Prefer Not To Say',
                value: 'Prefer Not To Say'
            },
        ]

        return (
            <div className="create-profile">
                <div className="container">
                    <div className="row">
                        <div className="col-md-8 m-auto">
                            <h1 className="display-4 text-center">Edit Your Profile</h1>
                            <form onSubmit={this.onSubmit} >
                                <TextField 
                                    placeholder="* Profile Handle"
                                    name="handle"
                                    value={this.state.handle}
                                    onChange={this.onChange}
                                    error={errors.handle}
                                    info="A unique handle for your profile URL. Your full name, company name, nickname, etc"
                                    />

                                <SelectList 
                                    placeholder="* Status"
                                    name="status"
                                    value={this.state.status}
                                    onChange={this.onChange}
                                    error={errors.status}
                                    options={options}
                                    info="Give you an idea of your career"
                                    />

                                <TextField 
                                    placeholder="Profile Company"
                                    name="company"
                                    value={this.state.company}
                                    onChange={this.onChange}
                                    error={errors.company}
                                    info="Where You Work"
                                    />

                                <TextField 
                                    placeholder="Website"
                                    name="website"
                                    value={this.state.website}
                                    onChange={this.onChange}
                                    error={errors.website}
                                    info="Could be your own or a company website"
                                    />  

                                <TextField 
                                    placeholder="Location"
                                    name="location"
                                    value={this.state.location}
                                    onChange={this.onChange}
                                    error={errors.location}
                                    info="Where you live right now"
                                    />

                                <TextField 
                                    placeholder="* Skills"
                                    name="skills"
                                    value={this.state.skills}
                                    onChange={this.onChange}
                                    error={errors.skiils}
                                    info="Please use (,) comma seperated values"
                                    /> 

                                <TextField 
                                    placeholder="Github Profile"
                                    name="githubusername"
                                    value={this.state.githubusername}
                                    onChange={this.onChange}
                                    error={errors.githubusername}
                                    info="If you want to show your github repos."
                                    />

                                <TextArea 
                                    placeholder="Short Bio"
                                    name="bio"
                                    value={this.state.bio}
                                    onChange={this.onChange}
                                    error={errors.bio}
                                    info="A short summary about you."
                                    />

                                <div className="mb-3">
                                    <button type="button"
                                    onClick={() =>{
                                        this.setState(prevState => ({
                                            displaySocialInputs: !prevState.displaySocialInputs
                                        }))
                                    }} className="btn btn-light">
                                        Add Social Network Links
                                    </button>
                                    <span className="text-muted">  Optional</span>
                                </div>
                                {socialInputs}
                                <input type="submit" value="submit" className="btn btn-info btn-block mt-4" />
                            </form>

                        </div>    
                    </div>
                </div>
            </div>
        )
    }
}

CreateProfile.propTypes = {
    createProfile: PropTypes.func.isRequired,
    getCurrentProfile: PropTypes.func.isRequired,
    profile: PropTypes.object.isRequired,
    errors: PropTypes.object.isRequired
}

const mapStateToProps = (state) =>({
    profile: state.profile,
    errors: state.errors
})

export default connect(mapStateToProps, { createProfile, getCurrentProfile } )(withRouter(CreateProfile))